import React from "react"

function Project({ title, isDeployed, deployLink, imageSource, description, technologiesUsed, repoLink }) {

    // Attach link to deployed app on image if there is a deployed link
    const imageElement = () => {
        let image = <img
            class="card-img-top"
            src={imageSource}
            alt="Card caption"
        />
        if (isDeployed === "true") {
            image = <div>
                    <a target="_blank" href={deployLink}>{image}</a>
                    <p class="project-click-description">click image for deployed app</p>
                </div>
        }

        return image
    }

    return (
        <div class="card col-10 col-md-5 col-xl-4 mb-4">
            {imageElement()}
            <div class="card-body">
                <p class="card-text">
                    {description}
                </p>
                <p class="card-text"><b>Technologies Used: </b>{technologiesUsed}</p>
                <center><a
                href={repoLink}
                class="repo-link btn btn-primary"
                target="_blank"
                >Github Repository</a
                ></center>
            </div>
        </div>
    )
}

export default Project